@import './sass/variables';

.collapse {
  position: relative;
  display: flex;
  flex-direction: column;

  max-width: 1200px;
  color: $primary-color;


  h4 {
    
    background-color: $primary-color;
    color: white;
    padding-block: 0.8rem;
    border-radius: 5px;
    margin: 0;
    padding-inline: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    }

    p {
    background-color:$background-color;
      padding: 1rem;
      border-radius: 5px;
      margin-block: -10px;
      position: relative;
      z-index: -1;
      top: -0.1rem;
      opacity: 0;
      max-height: 0px;
      overflow: hidden;
    transition: opacity 1.5s ease, max-height 1.5s ease; // Augmentez les durées de transition selon vos préférences
  }

  .visible {
        opacity: 1;
        max-height: 200px;
    
      }


      .open {
        color:white;
        transform: rotate(0deg); 
        transition: transform 0.5s ease; 
      }
      
      .closed {
        transform: rotate(-180deg); 
        transition: transform 0.5s ease; 
      }
      
       
    }
 


